@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&display=swap");

* {
  font-family: "Noto Sans KR", sans-serif;
  font-style: normal;

  padding: 0;
  margin: 0;
}

body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  overflow-x: hidden;
}

img {
  -webkit-user-drag: none;
}
